:root {
  --primary-font: "Epilogue", "sans serif";
  --dark-green: #1e2827;
  --black: #141414;
}

.about {
  max-width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 50px;
  display: flex;
}

.div__paragraph {
  max-width: 100%;
  height: auto;
  flex-direction: row;
  gap: 40px;
  display: flex;
}

.about__title {
  color: #1e2827;
  font-family: Epilogue;
  font-size: 40px;
  font-weight: 600;
  line-height: 44%;
}

.paragraph1, .paragraph2 {
  color: #141414;
  letter-spacing: -2%;
  text-align: normal;
  font-family: Epilogue;
  font-size: 14px;
  font-weight: 300;
  line-height: 18px;
}

.about-gif {
  max-width: 100%;
  justify-content: flex-end;
  display: flex;
}

.watch__gif {
  max-width: 100%;
  height: auto;
}

@media screen and (max-width: 768px) {
  .div__paragraph {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 40px;
    display: flex;
  }

  .about {
    margin-left: 20px;
    margin-right: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1280px) {
  .about__title {
    font-size: 32px;
  }

  .about-gif {
    justify-content: center;
  }

  .div__paragraph {
    max-width: 100%;
    height: auto;
    flex-direction: row;
    justify-content: space-around;
    gap: 20px;
    display: flex;
  }

  .about {
    display: flex;
  }

  .watch__gif {
    max-width: 100%;
    height: auto;
    margin-left: 50px;
  }

  .paragraph1, .paragraph2 {
    max-width: 30%;
  }
}

@media screen and (min-width: 1280px) {
  .paragraph1 {
    max-width: 40%;
    padding-left: 170px;
  }

  .paragraph2 {
    max-width: 30%;
  }

  .about {
    padding: 50px;
  }

  .about-gif {
    position: relative;
    top: 50px;
    left: 400px;
  }

  .watch__gif {
    width: 850px;
  }
}

